import React, { Component } from 'react';
import { Icon, Form } from 'antd';
import { BtnAddNew, ButtonGroup, Btn } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'formMenuTabTop' })
class MenuTabTop extends Component {
  state = {};
  render() {
    return (
      <ButtonGroup>
        <BtnAddNew
          title="To lp tn ch"
          href="#/students/statisticsDataForOpenClass/createNewClass"
        />
        <Btn>
          <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
          In k hoch
        </Btn>

        <Btn>
          <Icon type="setting" />
          Xut Excel
        </Btn>
      </ButtonGroup>
    );
  }
}

export default MenuTabTop;



