import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime } from 'dump/data';
import { BtnSearch } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
          <Col span={6}>
            <Form.Item label="Hc k:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Nm hc:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label=" ">
              <BtnSearch />
            </Form.Item>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span={6}>
            <Checkbox /> SV hc ln 1
          </Col>
          <Col span={6}>
            <Checkbox /> SV cha hc
          </Col>
          <Col span={6}>
            <Checkbox /> SV hc li
          </Col>
          <Col span={6}>
            <Checkbox /> SV hc ngnh 2
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommon;



