import React, { Component } from 'react';
import { Table } from 'antd';
import { setTableCourseListForOpenClass } from 'dump/data';

class TableCourseList extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'K hiu',
        dataIndex: 'kyHieu',
        key: 'kyHieu',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'tenHocPhan',
        key: 'tenHocPhan',
      },
      {
        title: 'S tn ch',
        dataIndex: 'soTC',
        key: 'soTC',
      },
      {
        title: 'S lp hnh chnh',
        dataIndex: 'soLopHanhChinh',
        key: 'soLopHanhChinh',
      },
      {
        title: 'S SV hc ln 1',
        dataIndex: 'soSVHocLan1',
        key: 'soSVHocLan1',
      },
      {
        title: 'S SV cha hc',
        dataIndex: 'soSVChuaHoc',
        key: 'soSVChuaHoc',
      },
      {
        title: 'S SV hc li',
        dataIndex: 'soSVChuaHoc',
        key: 'soSVChuaHoc',
      },
      {
        title: 'S SV hc ngnh 2',
        dataIndex: 'soSVHocNganh2',
        key: 'soSVHocNganh2',
      },
      {
        title: 'Tng s SV',
        dataIndex: 'tongSoSV',
        key: 'tongSoSV',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <Table
        bordered
        rowSelection={this.rowSelection}
        dataSource={setTableCourseListForOpenClass}
        columns={this.columns}
      />
    );
  }
}

export default TableCourseList;



